.container {
	width: 100%;
	position: relative;
	overflow: hidden;
}

h1.main, p.demos {
	animation-delay: 18s;
}
.sp-container {
    
	position: fixed;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100%;
	z-index: 0;
	background: radial-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.3) 35%, rgba(0, 0, 0, 0.7));
}
.sp-content {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0px;
	top: 0px;
	z-index: 1000;
}
.sp-container h2 {
	position: absolute;
	top: 50%;
    
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

	line-height: 100px;
	height: 100px;
	margin-top: -50px;
	font-size: 1rem;
	width: 100%;
	text-align: center;
	color: transparent;
	animation: blurFadeInOut 3s ease-in backwards;
	
}

.sp-container h2.frame-1 {
    font-size: 2rem;
	animation-delay: 0s;
}
.sp-container h2.frame-2 {
    font-size: 2rem;
	animation-delay: 3s;
}
.sp-container h2.frame-3 {
    font-size: 2rem;
	animation-delay: 6s;
}
.sp-container h2.frame-4 {
    font-size: 2rem;
	animation-delay: 9s;
}
.sp-container h2.frame-5 {
	animation: none;
	color: white;
	text-shadow: 0px 0px 1px #fff;
}

.sp-container h2.frame-5 span {
	animation: blurFadeIn 3s ease-in 12s backwards;
	text-shadow: 1px 2px 3px #000;
	height: 60px;
	line-height: 60px;
}
.sp-container h2.frame-5 span:nth-child(2) {
	animation-delay: 13s;
	text-shadow: 1px 2px 3px #000;
}
.sp-container h2.frame-5 span:nth-child(3) {
	animation-delay: 14s;
	text-shadow: 1px 2px 3px #000;
}

.sp-container h2.frame-5 span:nth-child(4) {
	animation-delay: 15s;
    font-size: 0.5rem;
    position: fixed;
    bottom:25%;
    right:10%;
    text-shadow: 1px 2px 3px #000;
    display: flex;
    align-items: center;
}

.sp-container h2.frame-5 span:nth-child(4):hover{
	color: #fad207;
}

@media all and (max-width: 768px){
    .sp-container {min-width: 375px;}
    .sp-container h2{font-size: 0.55rem;}
    .sp-container h2.frame-1{font-size: 0.75rem;}
    .sp-container h2.frame-2{font-size: 0.75rem;}
    .sp-container h2.frame-3{font-size: 0.75rem;}
    .sp-container h2.frame-4{font-size: 0.75rem;}
}

.sp-globe {
	position: absolute;
	width: 282px;
	height: 273px;
	left: 50%;
	top: 50%;
	margin: -137px 0 0 -141px;
	background: transparent url(http://web-sonick.zz.mu/images/sl/globe.png) no-repeat top left;
	animation: fadeInBack 3.6s linear 14s backwards;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=30)";
	filter: alpha(opacity=30);
	opacity: 0.3;
	transform: scale(5);
}
.sp-circle-link {
	position: absolute;
	left: 50%;
	bottom: 100px;
	margin-left: -50px;
	text-align: center;
	line-height: 100px;
	width: 100px;
	height: 100px;
	background: #fff;
	color: #3f1616;
	font-size: 25px;
	border-radius: 50%;
	animation: fadeInRotate 1s linear 16s backwards;
	transform: scale(1) rotate(0deg);
}
.sp-circle-link:hover {
	background: #85373b;
	color: #fff;
}
/**/
/**/
/**/

@keyframes blurFadeInOut {
	0% {
		opacity: 0;
		text-shadow: 0px 0px 40px #fff;
		transform: scale(1.3);
	}
	20%, 75% {
		opacity: 1;
		text-shadow: 0px 0px 1px #fff;
		transform: scale(1);
	}
	100% {
		opacity: 0;
		text-shadow: 0px 0px 50px #fff;
		transform: scale(0);
	}
}
@keyframes blurFadeIn {
	0% {
		opacity: 0;
		text-shadow: 0px 0px 40px #fff;
		transform: scale(1.3);
	}
	50% {
		opacity: 0.5;
		text-shadow: 0px 0px 10px #fff;
		transform: scale(1.1);
	}
	100% {
		opacity: 1;
		text-shadow: 0px 0px 1px #fff;
		transform: scale(1);
	}
}
@keyframes fadeInBack {
	0% {
		opacity: 0;
		transform: scale(0);
	}
	50% {
		opacity: 0.4;
		transform: scale(2);
	}
	100% {
		opacity: 0.2;
		transform: scale(5);
	}
}
@keyframes fadeInRotate {
	0% {
		opacity: 0;
		transform: scale(0) rotate(360deg);
	}
	100% {
		opacity: 1;
		transform: scale(1) rotate(0deg);
	}
}

/* Recruit button */

/* motion */
@keyframes gradient {
  0% {
    background-position: 14% 0%;
  }
  50% {
    background-position: 87% 100%;
  }
  100% {
    background-position: 14% 0%;
  }
}
@keyframes border {
  0% {
    -webkit-clip-path: polygon(0% 100%, 4px 100%, 4px 4px, 216px 4px, 216px 66px, 4px 66px, 4px 100%, 100% 100%, 100% 0%, 0% 0%);
  }
  25% {
    -webkit-clip-path: polygon(0% 100%, 4px 100%, 4px 4px, 216px 4px, 216px 66px, 216px 66px, 216px 100%, 100% 100%, 100% 0%, 0% 0%);
  }
  50% {
    -webkit-clip-path: polygon(0% 100%, 4px 100%, 4px 4px, 216px 4px, 216px 4px, 216px 4px, 216px 4px, 216px 4px, 100% 0%, 0% 0%);
  }
  75% {
    -webkit-clip-path: polygon(0% 100%, 4px 100%, 4px 4px, 4px 4px, 4px 4px, 4px 4px, 4px 4px, 4px 4px, 4px 0%, 0% 0%);
  }
  100% {
    -webkit-clip-path: polygon(0% 100%, 4px 100%, 4px 100%, 4px 100%, 4px 100%, 4px 100%, 4px 100%, 4px 100%, 4px 100%, 0% 100%);
  }
}

